
import Vue from 'vue'
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import Badge from 'vux/src/components/badge/index.vue'
import AjaxPlugin from 'vux/src/plugins/ajax/index.js'
import ToastPlugin from 'vux/src/plugins/toast/index.js'
import dateFormat from 'vux/src/tools/date/format.js'

import PullTo from 'vue-pull-to'
import { fetch } from '@/api/base'
import { checkLoginToLogin } from '@/api/login'

Vue.use(ToastPlugin)
Vue.use(AjaxPlugin)

export default {
name: 'withdrawal',
components: {
XHeader,
XTable,
Badge,
PullTo
},
methods: {
loadmore (loaded) {




this.getListData(loaded)
},
stateChange (state) {
if (state === 'pull' || state === 'trigger') {
this.iconLink = '#icon-arrow-bottom'
} else if (state === 'loading') {
this.iconLink = '#icon-loading'
} else if (state === 'loaded-done') {
this.iconLink = '#icon-finish'
}
},
getListData (loaded) {
fetch('/api/v1/agentbanks/withdrawalslist', {
limit: this.limit,
page: this.page
}, response => {
let data = response.data
let msg = data.msg || data.error || '请求失败'
if (data.code !== 0) {
this.$vux.toast.text(msg, 'middle')
return false
}
if (data.data.count === this.dataList.length) {
loaded('done')
return false
}
data.data.list.forEach(content => {
content.createdAt = dateFormat(new Date(content.createdAt), 'YYYY-MM-DD HH:mm:ss')
content.isSuccessText = content.isSuccess ? '提现成功' : '审核中'
})
this.dataList = this.dataList.concat(data.data.list)
this.count = data.data.count
this.page++
}, error => {
console.log(error)
}, 'GET')
}
},
data () {
return {
dataList: [],
iconLink: '',
page: 0,
count: 0,
limit: 15
}
},
created () {
this.getListData()
checkLoginToLogin(this)
}
}



